.streak-photo{
    width: 40%;
    margin: auto;
 
  
    margin-top: 40px;
   
  }
  .streak-photo img{
    width: 100%;
  }

  
   .stacklanguage{
        
     width: 80%;
   
     display: flex;
     flex-direction: column;
     justify-content:space-around;
      
     
    
   
     padding-top: 40px;
     margin: auto;
  
  
    

}
 .stacklanguage img{
   width: 100%;
   height: 200px;
   margin: auto;
   padding-bottom: 30px;
 }
    
    
 


@media screen and (max-width:420px) {

  .streak-photo{
    width: 97%;
    margin: auto;
  
    margin-top: 40px;
   
  }
  .streak-photo img{
    width: 100%;
  }

  
  .stacklanguage{
        
     width: 97%;
     margin: auto;
     display: flex;
     flex-direction: column;
     
     gap:20px;
   
     padding-top: 20px;
  
  
    

}
 .stacklanguage img{
  width: 97%;
   margin: auto;
 }
    
    
 } 



