.homecss{
    width: 40%;
    line-height: 40px;
    margin-top: 10%;
    margin-left: 5%;
   
    height: 60vh;
     

}
.heding1{
    margin-bottom: 25px;
    font-size: 30px; 
}
.headingrole{
  color:rgb(225, 29, 72);
    font-size: 35px; 
}
.pargrapfh{
     font-size: 20px;
     margin-top: 25px;
     line-height: 30px;
     color: white;
}

.homecss button{
    margin-top: 30px;
}
.mainHome{
    display: flex;
    justify-content: space-evenly;
    
}

.image{
    margin-top: 130px;
    background:content-box;
  
    border-radius: 10px;
   
   
   

    
  
       
   
}
.imgcss img{
    border-radius: 10px;
    height:240px;
    width: 85%;
}

.btn1{
  
  padding: 8px 32px;
  font-size: 17px;
  text-transform: uppercase;
  background: yellow;
  color: black;
  border: 1px solid white;
  font-weight: 600;
  cursor: pointer;

}


@media screen and (max-width:820px){
    .homecss{
        width: 90%;
        text-align: center;
        margin: auto;
        
         
    
    }

    .heding1{
       
       font-size:35px;
       margin-top: 60px;
        
    }

    .mainHome{
        width: 100%;
       
        
        
        display: flex;
        align-items: center;
        text-align: center;
        align-content: center;
        flex-direction: column;
       
        
        
    }

     .headingrole{
        
        font-size: 39px;
        margin-top: 40px;
        
    }
   .pargrapfh{
    width: 90%;
    margin: auto;
     font-size: 30px;
     margin-top: 35px;
     line-height: 45px;
     color: white;
